import {
  IonButton,
  IonCard,
  IonCardContent,
  IonCol,
  IonInput,
  IonItem,
  IonLabel,
  IonRow,
  IonSelect,
  IonSelectOption,
  IonTextarea,
} from "@ionic/react";
import React from "react";
import { useOrderForm } from "../../hooks/useOrderForm";

interface OrderFormProps {
  form: ReturnType<typeof useOrderForm>;
}

const OrderForm: React.FC<OrderFormProps> = ({ form }) => {
  return (
    <>
      <IonCard>
        <IonCardContent>
          <IonItem lines="none">
            <IonLabel>Khch hng</IonLabel>
            <IonSelect
              okText="Chn"
              cancelText="Hy"
              interface="action-sheet"
              placeholder="Khch hng"
              value={form.fields?.customer}
              onIonChange={(e) =>
                form.setFieldsValue({ customer: e.detail.value! })
              }
            >
              {form.customers?.map((item) => (
                <IonSelectOption key={item.id} value={item.id}>
                  {item.name}
                </IonSelectOption>
              ))}
            </IonSelect>
          </IonItem>
        </IonCardContent>
      </IonCard>

      <IonCard>
        <IonCardContent>
          <IonItem lines="none">
            <IonLabel position="floating">Ghi ch</IonLabel>
            <IonTextarea
              onIonChange={(e) =>
                form.setFieldsValue({ note: e.detail.value! })
              }
            ></IonTextarea>
          </IonItem>
        </IonCardContent>
      </IonCard>

      <IonRow>
        {form.fields.lines.map((line, index) => (
          <IonCol
            key={index}
            size="12"
            sizeMd={form.fields.lines.length <= 1 ? "12" : "6"}
            style={{ padding: 0 }}
          >
            <IonCard>
              <IonCardContent>
                <IonItem>
                  <IonLabel>Sn phm</IonLabel>
                  <IonSelect
                    okText="Chn"
                    cancelText="Hy"
                    interface="action-sheet"
                    placeholder="Sn phm"
                    value={form.fields.lines[index].product}
                    onIonChange={(e) =>
                      form.setFieldsValue({
                        lines: form.fields.lines.map((line, i) =>
                          i === index
                            ? { ...line, product: e.detail.value! }
                            : line
                        ),
                      })
                    }
                  >
                    {form.products?.map((item) => (
                      <IonSelectOption key={item.id} value={item.id}>
                        {item.name}
                      </IonSelectOption>
                    ))}
                  </IonSelect>
                </IonItem>

                <IonItem>
                  <IonLabel>Kch c</IonLabel>
                  <IonSelect
                    okText="Chn"
                    interface="action-sheet"
                    cancelText="Hy"
                    placeholder="Kch c"
                    value={form.fields.lines[index].size}
                    onIonChange={(e) =>
                      form.setFieldsValue({
                        lines: form.fields.lines.map((line, i) =>
                          i === index
                            ? { ...line, size: e.detail.value! }
                            : line
                        ),
                      })
                    }
                  >
                    {form.products
                      ?.find((v) => v.id === line.product)
                      ?.sizes?.map((item, index) => (
                        <IonSelectOption key={index} value={item}>
                          {item}
                        </IonSelectOption>
                      ))}
                  </IonSelect>
                </IonItem>

                <IonItem>
                  <IonLabel>S lng</IonLabel>
                  <IonInput
                    type="number"
                    value={form.fields.lines[index].quantity || undefined}
                    style={{ textAlign: "right" }}
                    onIonChange={(e) =>
                      form.setFieldsValue({
                        lines: form.fields.lines.map((line, i) =>
                          i === index
                            ? { ...line, quantity: parseInt(e.detail.value!) }
                            : line
                        ),
                      })
                    }
                  ></IonInput>
                </IonItem>

                {form.fields.lines.length > 1 && (
                  <IonItem
                    button
                    detail={false}
                    onClick={() => form.removeLine(index)}
                  >
                    <IonLabel color="danger" style={{ textAlign: "center" }}>
                      Xa
                    </IonLabel>
                  </IonItem>
                )}
              </IonCardContent>
            </IonCard>
          </IonCol>
        ))}
      </IonRow>

      <IonButton expand="block" onClick={form.addLine} style={{ margin: 10 }}>
        Thm sn phm
      </IonButton>
    </>
  );
};

export default OrderForm;
